import { useConfigContext } from "../../contexts";
import { Mode } from "../../contexts/themeContext";
import Button from "../Button/Button";
import Input from "../Input/Input";
import "./ContrastRatios.css";
import React, { FC } from "react";

interface Props {
  theme: Mode;
}

const trashSvg = (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 16 16"
    width="16"
    height="16"
  >
    <path d="M11 1.75V3h2.25a.75.75 0 0 1 0 1.5H2.75a.75.75 0 0 1 0-1.5H5V1.75C5 .784 5.784 0 6.75 0h2.5C10.216 0 11 .784 11 1.75ZM4.496 6.675l.66 6.6a.25.25 0 0 0 .249.225h5.19a.25.25 0 0 0 .249-.225l.66-6.6a.75.75 0 0 1 1.492.149l-.66 6.6A1.748 1.748 0 0 1 10.595 15h-5.19a1.75 1.75 0 0 1-1.741-1.575l-.66-6.6a.75.75 0 1 1 1.492-.15ZM6.5 1.75V3h3V1.75a.25.25 0 0 0-.25-.25h-2.5a.25.25 0 0 0-.25.25Z"></path>
  </svg>
);

const ContrastRatios: FC<Props> = ({ theme }) => {
  const { config, setConfig } = useConfigContext();

  const addStep = (mode: Mode) => {
    const newSteps = [
      ...config[mode].steps,
      config[mode].steps[config[mode].steps.length - 1] + 1,
    ];
    setConfig(`${mode}.steps`, newSteps);
  };

  const updateContrastRatio = (index: number, value: number) => {
    const newSteps = [...config[theme].steps];
    newSteps[index] = Number(value);
    setConfig(`${theme}.steps`, newSteps);
  };

  const removeStep = (mode: Mode, index: number) => {
    const newSteps = [...config[mode].steps];
    newSteps.splice(index, 1);
    setConfig(`${mode}.steps`, newSteps);
  };

  return (
    <div className="ContrastRatios">
      {config[theme].steps.map((step, index) => (
        <div className="step" key={`step-${theme}-${index}-${step}`}>
          <Input
            label={`Step ${index}`}
            type="number"
            value={step}
            onChange={(event) => {
              updateContrastRatio(index, Number(event.target.value));
            }}
          />
          <Button
            variant="danger"
            className="remove-button"
            onClick={() => removeStep(theme, index)}
          >
            {trashSvg}
          </Button>
        </div>
      ))}
      <Button className="add-button" onClick={() => addStep(theme)}>
        Add Step
      </Button>
    </div>
  );
};

export default ContrastRatios;
